import React from 'react';
import PropTypes from 'prop-types';


const Result = props =>(
<div id="screen_3" className="container">
		<div id="back" className="back_btn"></div>
		<div id="header_3" className="header">
		</div>
    <div className="sub_header">2 очень простых условия и</div>
    <div id="header_4" className="header">
		</div>
		<div className="sub_header">в розыгрыше айфона и</div>
		<div className="sub_header">других 500 крутых призов</div>
		<div className="img"></div>
		<div onClick={props.sub} id="subscribe" className="btn solid_red">подписаться на <br/> sattarovfamily</div>
		<div id="bot" onClick={props.bot} className="btn solid_red">написать в лс</div>
		<div className="sub_header">расскажи друзьям о своих<br/>результатах егэ</div>
		<div onClick={props.post} className="btn orange">опубликовать<br/>запись</div>
	</div>
);

Result.propTypes = {
	id: PropTypes.string.isRequired,
  sub: PropTypes.func.isRequired,
	bot: PropTypes.func.isRequired,
	post: PropTypes.func.isRequired,
};

export default Result;
